.over {
  &-wrapper {
    width: 100%;
    display: flex;
  }
  &-info {
    width: 60%;
    padding-right: 52px;
    h2 {
      font-size: 4.4em;
      padding-bottom: 52px;
    }
  }
  &-img {
    margin-left: auto;
    width: 20%;
    @include bg-color(background-color);
    img {
      border-radius: 6px;
      mix-blend-mode: var(--image-blend-mode);
    }
  }
}